<template>
    <div>
        <el-form ref="form" :model="form" label-width="80px" :rules="rules" style="background: rgba(255,255,255,0.4);padding:40px 30px">
            <el-form-item label="tid">
                {{form.tid}}
            </el-form-item>
            <el-form-item label="" prop="user">
                <el-input v-model="form.user"></el-input>
            </el-form-item>
            <el-form-item label="" prop="cname">
                <el-select v-model="form.cname" :placeholder="form.cname">
                    <el-option v-for="v in classes" :value="v.cname" :key="v.cid" :label="v.cname"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('form')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    export default {
        name:'teacherUpdate',
        data(){
           return{
               form:{
                   tid:'',
                   user:'',
                   cname:''
               },
               classes:[],
               rules:{
                   user:[
                       {required: true, message: '', trigger: 'blur'},
                       {min: 1, max: 20, message: ' 1  20 ', trigger: 'blur'}
                   ],
                   cname: [
                       { required: true, message: '', trigger: 'change' }
                   ],
               }
           }
        },
        methods:{
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$http.post('/home/teacher/teacherUpdate.php',this.form).then(res=>{
                            if(res.body=='ok'){
                                this.$router.push('manageTeacher')
                            }else if(res.body=='error'){
                                this.$message({
                                    showClose: true,
                                    message: '',
                                    type: 'warning'
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        mounted(){
            this.$http.post('/home/teacher/teacherQuery.php',this.$route.query).then(res=>{
                this.form = res.body[1]
                this.classes = res.body[0];
            })
        },
        beforeCreate(){
            if(!localStorage.admin){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
                this.$router.push({name:'login'})
            }
        }
    }
</script>
<style>

</style>


