<template>
    <div>
        <el-form ref="form" :model="form" label-width="80px" :rules="rules" style="background: rgba(255,255,255,0.4);padding:40px 30px">
            <el-form-item label="" prop="sid">
                <el-select v-model="form.sid" placeholder="">
                    <el-option v-for="v in stage" :value="v.sid" :key="v.sid" :label="v.sname"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select v-model="form.type" placeholder="">
                    <el-option v-for="v in type" :value="v.id" :key="v.id" :label="v.name"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="title">
                <el-input type="textarea" v-model="form.title" placeholder=" _  :| "></el-input>
                <span class="el-form-item__error">*_A:|B:(_)</span>
            </el-form-item>
            <el-form-item label="" prop="answer">
                <el-input type="textarea" v-model="form.answer" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="exp">
                <el-input type="textarea" v-model="form.exp" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="score">
                <el-input v-model="form.score" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('form')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    export default {
        name:'dirAdd',
        data(){
           return{
               form:{
                   sid:'',
                   type:'',
                   title:'',
                   answer:'',
                   exp:'',
                   score:'',
               },
               rules:{
                   sid:[
                       {required: true, message: '', trigger: 'change'},
                   ],
                   type:[
                       {required: true, message: '', trigger: 'change'},
                   ],
                   title:[
                       {required: true, message: '', trigger: 'blur'},
                   ],
                   answer:[
                       {required: true, message: '', trigger: 'blur'},
                   ] ,
                   exp:[
                       {required: true, message: '', trigger: 'blur'},
                   ],
                   score:[
                       {required: true, message: '', trigger: 'blur'},
                   ]
               },
               stage:[],
               type:[],
           }
        },
        methods:{
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$http.post('/home/test/testAdd.php',this.form).then(res=>{
                            if(res.body=='ok'){
                                this.$router.push('teacherTest')
                            }else if(res.body=='error'){
                                this.$message({
                                    showClose: true,
                                    message: '',
                                    type: 'warning'
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        mounted(){
            this.$http.post('/home/stage/manageStage.php').then(res=>{
                this.stage=res.body;
            }),
            this.$http.post('/home/test/teacherType.php').then(res=>{
                this.type=res.body;
            })
        },
        beforeCreate(){
            if(!localStorage.teacher){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
                this.$router.push({name:'login'})
            }
        },
    }
</script>
<style>

</style>


